.my-masonry-grid { /* Not needed if autoprefixing */ /* Not needed if autoprefixing */
  display: flex;
  margin-left: -10px; /* gutter size offset */
  width: auto;
}
.my-masonry-grid_column {
  padding-left: 30px; /* gutter size */
  background-clip: padding-box;
}

/* Style your items */
.my-masonry-grid_column > div { /* change div to reference your elements you put in <Masonry> */
 
  margin-bottom: 30px;
}

.model{
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f9f9f9;
  transition:  opacity .4s  ease,  visibility .4s ease,  -webkit-transform .5s ease-in-out;
  transition:  opacity .4s  ease,  visibility .4s ease,  transform .5s ease-in-out ;
  transition:  opacity .4s  ease,  visibility .4s ease,  transform .5s ease-in-out,  -webkit-transform .5s ease-in-out;
  visibility: hidden;
  opacity: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
  overflow: hidden;
  z-index: 999;
}
.open{
  visibility: visible;
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);

}
.model  img{
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
  display: block;
  line-height: 0;
  box-sizing: border-box;
  padding: 20px 0 20px;
  margin: 0 auto;
}
.open svg{
  position: fixed;
  top: 10px;
  right: 10px;
  width: 2rem;
  height: 2rem;
  padding: 5px;
  background-color: rgba(0,0,0,0.4);
  color: white;
  cursor: pointer;
}
.pics img{
  cursor: pointer;
}
.cardContainer{
  border-radius: 5px  black solid;
